import React, { useState } from "react"
import { useSelector, shallowEqual } from "react-redux"
import { Link } from "gatsby"
import { PROJECT_LINK, PREVIEW, VID } from "../../project-config"
import { footerContent } from "./footerContent"
import AnchorElement from "../../functions/AnchorElement.jsx"
import Logo from "../../assets/img/balodana/logoGray.jpg"
import Paypal from "../../assets/img/balodana/footer/paypal.png"
import SSL from "../../assets/img/balodana/footer/ssl.png"
import classes from "./Styles/BDFooter.module.css"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { library } from "@fortawesome/fontawesome-svg-core"
import { fab } from "@fortawesome/free-brands-svg-icons"
import { faChevronUp, faChevronDown } from "@fortawesome/free-solid-svg-icons"
library.add(fab, faChevronUp, faChevronDown)

function Footer() {
  const [hovered, setHovered] = useState(false)
  const toggleHover = () => setHovered(!hovered)

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  )
  const [activeFooterSection, setActiveFooterSection] = useState("")

  if (isMobileState) {
    const handleOnClickFooterSection = e => {
      const { target: clickedFooterSectionTarget } = e.target.dataset
      setActiveFooterSection(
        activeFooterSection !== clickedFooterSectionTarget &&
          clickedFooterSectionTarget
      )

      console.info(
        "activeFooterSection",
        activeFooterSection,
        clickedFooterSectionTarget
      )
    }

    const handleWhatIconToDisplay = footerSectionName => {
      return activeFooterSection === footerSectionName
        ? "chevron-up"
        : "chevron-down"
    }
    return (
      <footer style={{ clear: "both" }}>
        <div className={classes.mFooter}>
          <div className="columns " style={{ maxWidth: "100%" }}>
            <div className="footer_Line">
              <div>
                <div className={classes.footerLogo}>
                  <img
                    src={Logo}
                    alt="Balodana Logo"
                    style={{ height: "40px" }}
                  />
                </div>
              </div>
            </div>
            <div className={classes.mLinks}>
              <div
                className={classes.mLinksHeader}
                id="shop-menu"
                onClick={e => handleOnClickFooterSection(e)}
                data-target="shop"
                aria-controls="shop"
                aria-expanded={activeFooterSection === "shop"}
              >
                <span></span>
                <span>SHOP</span>
                <FontAwesomeIcon
                  style={{ marginLeft: "10px" }}
                  icon={handleWhatIconToDisplay("shop")}
                  data-target="shop"
                />
                {/* <i className={handleWhatIconToDisplay("shop")} data-target="shop"></i> */}
              </div>
              <div
                className={classes.menuFooter}
                aria-labelledby="shop-menu"
                role="region"
                id="shop"
                style={{
                  display: activeFooterSection === "shop" ? "block" : "none",
                }}
              >
                <ul>
                  <li>Schedule Appointment</li>
                  <li>Custom Everyday</li>
                  <li>Custom Couture</li>
                  <li>Shop All</li>
                  <li>Gift Cards</li>
                  <li>Sale</li>
                </ul>
              </div>

              <div
                className={classes.mLinksHeader}
                id="info-menu"
                onClick={e => handleOnClickFooterSection(e)}
                data-target="info"
                aria-controls="info"
                aria-expanded={activeFooterSection === "info"}
              >
                <span></span>
                <span>INFO</span>
                <FontAwesomeIcon
                  style={{ marginLeft: "10px" }}
                  icon={handleWhatIconToDisplay("info")}
                  data-target="info"
                />
                {/* <i className={handleWhatIconToDisplay("info")} data-target="info"></i> */}
              </div>
              <div
                className={classes.menuFooter}
                aria-labelledby="info-menu"
                role="region"
                id="info"
                style={{
                  display: activeFooterSection === "info" ? "block" : "none",
                }}
              >
                <ul>
                  <li>How It Works</li>
                  <li>Shipping Info</li>
                  <li>Happiness Guarantee</li>
                  <li>Measurement Guide</li>
                  <li>Contact Us</li>
                </ul>
              </div>

              <div
                className={classes.mLinksHeader}
                id="bd-menu"
                onClick={e => handleOnClickFooterSection(e)}
                data-target="bd"
                aria-controls="bd"
                aria-expanded={activeFooterSection === "bd"}
              >
                <span></span>
                <span>MY BALODANA</span>
                <FontAwesomeIcon
                  style={{ marginLeft: "10px" }}
                  icon={handleWhatIconToDisplay("bd")}
                  data-target="bd"
                />
                {/* <i className={handleWhatIconToDisplay("bd")} data-target="bd"></i> */}
              </div>
              <div
                className={classes.menuFooter}
                aria-labelledby="bd-menu"
                role="region"
                id="bd"
                style={{
                  display: activeFooterSection === "bd" ? "block" : "none",
                }}
              >
                <ul>
                  <li>Sign Up</li>
                  <li>My Profile</li>
                  <li>My Orders</li>
                </ul>
              </div>

              <div
                className={classes.mLinksHeader}
                id="company-menu"
                onClick={e => handleOnClickFooterSection(e)}
                data-target="company"
                aria-controls="company"
                aria-expanded={activeFooterSection === "company"}
              >
                <span></span>
                <span>COMPANY</span>
                <FontAwesomeIcon
                  style={{ marginLeft: "10px" }}
                  icon={handleWhatIconToDisplay("company")}
                  data-target="company"
                />
                {/* <i className={handleWhatIconToDisplay("company")} data-target="company"></i> */}
              </div>
              <div
                className={classes.menuFooter}
                aria-labelledby="company-menu"
                role="region"
                id="company"
                style={{
                  display: activeFooterSection === "company" ? "block" : "none",
                }}
              >
                <ul>
                  <li>About</li>
                  <li>Team</li>
                  <li>Careers</li>
                  <li>Press</li>
                  <li>Blog</li>
                  <li>For Brands</li>
                </ul>
              </div>

              <div className={classes.mIcons}>
                <span className={classes.icons}>
                  <FontAwesomeIcon
                    icon={hovered ? ["fab", "facebook"] : ["fab", "facebook-f"]}
                    onMouseEnter={toggleHover}
                    onMouseLeave={toggleHover}
                  />
                  <FontAwesomeIcon icon={["fab", "twitter"]} />
                  <FontAwesomeIcon icon={["fab", "instagram"]} />
                  <FontAwesomeIcon icon={["fab", "pinterest-p"]} />
                </span>
                <div className={classes.securedBy}>
                  <span className={classes.securedByText}>Secured By</span>
                  <div className={classes.securedByImages}>
                    <img src={Paypal} alt="Paypal logo" />
                    <img src={SSL} alt="Paypal logo" />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </footer>
    )
  } else {
    return (
      <footer style={{ clear: "both" }}>
        {/* <footer style={{width: "100%"}}> */}
        <div className={classes.footer}>
          <img src={Logo} />
          <div className={classes.links}>
            <span>SHOP</span>
            <ul>
              <li>Schedule Appointment</li>
              <li>Custom Everyday</li>
              <li>Custom Couture</li>
              <li>Shop All</li>
              <li>Gift Cards</li>
              <li>Sale</li>
            </ul>
          </div>
          <div className={classes.links}>
            <span>INFO</span>
            <ul>
              <li>How It Works</li>
              <li>Shipping Info</li>
              <li>Happiness Guarantee</li>
              <li>
                <a
                  href="https://www.balodana.com/customer-service/measurement-guide"
                  target="_blank"
                >
                  Measurement Guide
                </a>
              </li>
              <li>Contact Us</li>
            </ul>
          </div>
          <div className={classes.links}>
            <span>MY BALODANA</span>
            <ul>
              <li>Sign Up</li>
              <li>My Profile</li>
              <li>My Orders</li>
            </ul>
          </div>
          <div className={classes.links}>
            <span>COMPANY</span>
            <ul>
              <li>About</li>
              <li>Team</li>
              <li>Careers</li>
              <li>Press</li>
              <li>Blog</li>
              <li>For Brands</li>
            </ul>
          </div>
          <div className={classes.links}>
            <span className={classes.icons}>
              <FontAwesomeIcon
                icon={hovered ? ["fab", "facebook"] : ["fab", "facebook-f"]}
                // onMouseEnter={toggleHover}
                // onMouseLeave={toggleHover}
              />
              <FontAwesomeIcon
                icon={hovered ? ["fab", "twitter-square"] : ["fab", "twitter"]}
                // onMouseEnter={toggleHover}
                // onMouseLeave={toggleHover}
              />
              <FontAwesomeIcon
                icon={hovered ? ["fab", "instagram"] : ["fab", "instagram"]}
                // onMouseEnter={toggleHover}
                // onMouseLeave={toggleHover}
              />
              <FontAwesomeIcon
                icon={hovered ? ["fab", "pinterest-p"] : ["fab", "pinterest-p"]}
                // onMouseEnter={toggleHover}
                // onMouseLeave={toggleHover}
              />
              {/* <FontAwesomeIcon icon={['fab', 'twitter']} />
              <FontAwesomeIcon icon={['fab', 'instagram']} />
              <FontAwesomeIcon icon={['fab', 'pinterest-p']} /> */}
            </span>
            <div className={classes.securedBy}>
              <span className={classes.securedByText}>Secured By</span>
              <div className={classes.securedByImages}>
                <img src={Paypal} alt="Paypal logo" />
                <img src={SSL} alt="Paypal logo" />
              </div>
            </div>
          </div>
        </div>
        <div className={classes.subFooter}>
          <div className={classes.servicePolicy}>
            TERMS OF SERVICE | PRIVACY POLICY
          </div>
          <div className={classes.copyright}>COPYRIGHT  BALODANA 2020</div>
        </div>
      </footer>
    )
  }
}

export default Footer
