import React from "react"

import encodeConverter from "../../../../functions/htmldecoder"
import Grid from "@material-ui/core/Grid"

import { PROJECT_LINK } from "../../../../project-config"
import { useLocation } from "@reach/router"

import classes from "../CategoryHeader/Styles/BDCategoryHeader.module.css"

export default function CategoryHeader({ data }) {
  const location = useLocation()
  const categoryImageState = data.image

  const filteredCatImage = categoryImageState
    ? categoryImageState.split(" ").join("%20")
    : categoryImageState

  return (
    <React.Fragment>
      <div
        className={
          location.pathname.includes("/search")
            ? classes.subNavWrapper2
            : classes.subNavWrapper
        }
        style={{
          background: location.pathname.includes("search/")
            ? null
            : `url(${PROJECT_LINK}/store${filteredCatImage})`,
        }}
      >
        <div className={classes.catInfo}>
          <div className="sub-nav-title-desc-wrapper">
            <Grid container justify="space-between" alignItems="center">
              <Grid item xs={12}>
                <h1
                  className={classes.catName}
                  dangerouslySetInnerHTML={{ __html: data.name }}
                ></h1>
                {location.pathname.includes("/search") ? (
                  <div className={classes.catDesc}>Search Results</div>
                ) : null}
              </Grid>
              <Grid item xs={12}>
                <div
                  className={classes.catDesc}
                  style={{
                    display: data.longdesc != "" ? `flex` : `none`,
                  }}
                >
                  <div
                    style={{
                      margin: "0",
                    }}
                    dangerouslySetInnerHTML={{
                      __html: encodeConverter(data.longdesc),
                    }}
                  ></div>
                </div>
              </Grid>
            </Grid>
          </div>
        </div>
        {/* <CategoryBreadcrumb /> */}
      </div>
    </React.Fragment>
  )
}
