import React, { useEffect, useState } from "react"
import { useSelector, shallowEqual, useDispatch } from "react-redux"
import Loading from "../../../AC-Loading/Loading.jsx"
import encodeConverter from "../../../../functions/htmldecoder"
import Async from "react-code-splitting"
import Grid from "@material-ui/core/Grid"

import HelmetSeo from "../../../../shared/components/AC-Helmet/HelmetSeo"

import {
  fetchCategoryFromDirectUrl,
  backToCategoryNormalize,
} from "../../../../redux/actions/categoryActions"
import { useLocation } from "@reach/router"

import { PROJECT_LINK } from "../../../../project-config"
import { usePrevious } from "../../../../functions/Utilities.js"

import classes from "../CategoryHeader/Styles/BDCollectionsHeader.module.css"

export default function CategoryHeader() {
  const location = useLocation()

  const dispatch = useDispatch()
  const [categoryNameRender, setCategoryNameRender] = useState("")

  const prevPathName = usePrevious(location.pathname)

  const navCats = useSelector(
    state => state.menuReducer.navCategory,
    shallowEqual
  )

  const categoryNameState = useSelector(
    state => state.categoryReducer.cat,
    shallowEqual
  )

  const categoryImageState = useSelector(
    state => state.categoryReducer.catImage,
    shallowEqual
  )

  const categoryFetchFlagState = useSelector(
    state => state.menuReducer.categoryFetchFlag,
    shallowEqual
  )

  const loading = useSelector(
    state => state.categoryReducer.loading,
    shallowEqual
  )

  const cidN = useSelector(state => state.categoryReducer.cidN, shallowEqual)

  /*Checking Category Name with URL Location if not match fetch the data again
  This funtion is for back button helper*/
  useEffect(() => {
    let prevPath = prevPathName
    console.info("category fetch flag", categoryFetchFlagState)

    if (
      (prevPath !== location.pathname && prevPath !== undefined) ||
      categoryFetchFlagState == true
    ) {
      console.info("category fetch flag", categoryFetchFlagState)
      dispatch(fetchCategoryFromDirectUrl())
    }

    console.info(
      "langCode -",
      `url:'${location.pathname}'`,
      `prevPath:'${prevPath}'`,
      prevPath !== location.pathname && prevPath !== undefined
    )

    return () => {
      console.info(
        "langCode +",
        `url:'${location.pathname}'`,
        `prevPath:'${prevPath}'`,
        `prevPath2:'${prevPathName}'`
      )
    }
  }, [location.pathname])

  useEffect(() => {
    if (categoryNameState) {
      console.error("WED---", categoryNameState, categoryNameRender)
      let nameState = categoryNameState
      if (categoryNameState.includes("&aacute;")) {
        nameState = categoryNameState.replace("&aacute;", "")
      }
      if (categoryNameState.includes("&eacute;")) {
        nameState = categoryNameState.replace("&eacute;", "")
      }
      if (categoryNameState.includes("&iacute;")) {
        nameState = categoryNameState.replace("&iacute;", "")
      }
      setCategoryNameRender(nameState)
    }
  }, [categoryNameState])

  return (
    <React.Fragment>
      <HelmetSeo
        title={categoryNameRender}
        desc={categoryNameRender}
        cid={cidN}
      />
      {loading ? null : (
        <div className={classes.outer}>
          <div className={classes.inner}>
            <img
              className={classes.image}
              src={`${PROJECT_LINK}/store${categoryImageState}`}
            />
            <div className={classes.info}>
              <h1 dangerouslySetInnerHTML={{ __html: categoryNameRender }}></h1>
              <p
                dangerouslySetInnerHTML={{
                  __html: encodeConverter(navCats.longdesc),
                }}
              ></p>
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  )
}
