import React, { useState } from "react"

import Modal from "@material-ui/core/Modal"
import Backdrop from "@material-ui/core/Backdrop"
import Fade from "@material-ui/core/Fade"

import Book from "../../../../assets/icons/book.svg"
import {
  Aiste,
  ANT,
  Delia,
  EUG,
  Lino,
  Samshek,
  Shantima,
  Sumissura,
  Tavrovska,
  Teyxo,
  Zaza,
} from "../../../../assets/img/balodana/designers/logos"

import classes from "../CategoryHeader/Styles/BDDesignerHeader.module.css"
import { PROJECT_LINK } from "../../../../project-config"

// const CategoryBreadcrumb = () => (
//   <Async load={import("../../../AC-Breadcrumb/BDCategoryBreadcrumb.jsx")} />
// );

const BDDesignerCategoryHeader = ({ data }) => {
  const findDesigner = a => {
    if (a.includes("/aiste"))
      return {
        name: "Aiste Anaite",
        banner: "/store/20180730431/images/Aiste_Carousel_1.jpg",
        balodanaSays:
          "Each of Aistes creations are [affordable] works of art. She calls her process fabric sculpting and if youre a fan of textured clothing, youre going to fall in love with them. The folds and textures are sewn into the clothing before its used to make actual garments. Aistes use of natural fabrics such as linen, hemp, and bamboo that are good for your body and for Mother Nature is a huge plus, as well.",
        year: 2000,
        from: "Vilnius, Lithuania",
        logo: Aiste,
        longDesc: `<p>I am Aiste. My cat Kacius and I live in nature. Together, we design clothes that make you feel beautiful, comfy and safe.</p>
          <p>I only use natural fabrics. Before sewing, I cut the fabric and soak it in water that is enriched with minerals that ensure the fabric wont shrink after use. Its also important to me to make sure that my fabric will take good care of your sensitive skin!</p>
          <p>Aistes Sculpture on Fabric process comes after that. A unique technique, invented by me, transforms the flab piece of natural fabric into a wonderful textile sculpture with twists and turns, peaks and valleys.</p>
          <p>The last step of my journey is that I design and sew your garment based on your body measurements and inspired by your personality and inner beauty.</p>`,
        values: `<p>Our values are those of love, beauty, and harmony and we achieve these through the cloth we sculpt, the geometry of our garments, and the quality of our fabrics.</p>
          <p>I believe in actually doing and not just saying. Therefore, my lifestyle as a designer is simple. Everything we do as a brand is done with the environment in mindwe must be careful with Nature because she is our biggest gift and we depend on her more than anything. Most people know this, but dont necessarily act based on this as a guiding value. Aiste Anaite aims to do and not just to say.</p>
          <p>The other biggest thing that we care about is our bodies. We need to care about our bodies and the reason I work in natural fabrics is to put the utmost importance on the safety of our bodies. Also, we all just want to feel good about ourselves and our bodies and what we wear is a way to achieve that. I want our clothes to achieve that value.</p>`,
        instagram: "@jcatbeauty",
      }
    else if (a.includes("/ashley")) {
      return {
        name: "Ashley Nell Tipton",
        banner: "/store/20180730431/images/ashley-carousel.jpg",
        balodanaSays:
          "We know first-hand the challenge of adapting patterns and designs for larger women, but the result is 100% worth it and is necessary for incorporation into brand line development. We love Ashleys use of pastel colors and unexpected details to create a sense of romance, sexiness and bold power in every garment.",
        year: 2012,
        from: "San Diego, CA - USA",
        logo: ANT,
        longDesc: `          <p>Plus-size designer and Project Runway-Reality TV star, Ashley Nell Tipton, 28, had taken the fashion 
          world by storm, putting her plus-size collection on the runway during New York Fashion Week 2015. 
          <strong>With that first ever plus sized collection she went on to win that season of Project Runway!!</strong> 
          Project Runway is an International Reality Competition that features Talented Designers from all over the world competing to win the title of Best Designer!</p>
          <p>In 2016 she partnered with JC Penney to create Boutique Plus - the first Plus Size Boutique within a major department store.</p>
          <p>Since then she has created a plus-size accessory line, plus-size eyeglass frames and has become one of the leading Plus-Size influencers and advocates.</p>
          <p>In 2017, she relaunched her own E-Commerce site and was ranked one of the top 2% new launches on the Shopify Ecommerce platform. She has been featured in USA today, Marie Claire, In-Style, Bustle, O, People Magazine and many other publications. She was recently selected for Forbes 30 under 30.</p>
          <p>With her new line of custom couture, Ashley is making her designs available to all body types and sizes, from petite to plus and everything in between. Additionally, her couture line will represent a new luxury offering for the Ashley Nell Tipton brand.</p>`,
        values: `<p>Ashley Nell Tipton brand is committed to the following three principles: Fit, Comfort and Durability. As a designer and member of the Plus-Size community, Ashley knows what it feels like to purchase clothing that is a compromise for what she would really prefer to buy and wear. She is committed to changing that paradigm.</p>`,
        instagram: "@jcatbeauty",
      }
    } else if (a.includes("/delia")) {
      return {
        name: "Delia von Hahn",
        banner: "/store/20180730431/images/Delia_Carousel_1.jpg",
        balodanaSays:
          "These basics are anything but basic. Delia von Hahn's daily luxuries will quickly become your go-to staples for work and weekend. We especially love that she works in bamboo, which is not only sustainable and durable, but also feels so incredibly soft!",
        year: 2014,
        from: "Vancouver, Canada",
        logo: Delia,
        longDesc: `<p>Delia von Hahn Designs was established with a vision to create luxury, comfortable clothing for women so they can look and feel beautiful. This vision has formed the basis of all Delia von Hahn collections since the brand was launched in 2014.</p>
          <p>Our philosophy focuses on high-quality design and construction, and the use of sustainable fabrics.</p>
          <p>Delia von Hahn developed her technical skills at the London College of Fashion and launched her eponymous label in Vancouver, Canada after years of experience as a fashion designer. The early success of the label propelled Delia to show her collections around the world. Delias eye for craftsmanship and quality is shown in each piece she designs.</p>`,
        values: `<p>Delia von Hahns philosophy is to empower women to look beautiful and feel good. Our designs are classic, timeless pieces that feature high-quality construction and sustainable fabrics. We seek to build strong relationships with our customers, suppliers, workers and the community and to treat everyone with integrity and respect. Our collections are produced in Canada. A percentage of sales is paid to charity.</p>`,
        instagram: "",
      }
    } else if (a.includes("/eug-fashion")) {
      return {
        name: "EUG Fashion",
        banner: "/store/20180730431/images/eug-carousel-1.jpg",
        balodanaSays: `EUG represents some of the most innovative design trends in Europe, combining lush fabrics, edgy structures and comfortable quality. One of their specialties is making "modest" dressing fun, and we also appreciate their affordable pricing!`,
        year: 2006,
        from: "Sofia, Bulgaria",
        logo: EUG,
        longDesc: `<p>My name is Gergana, designer at EUG fashion. My work is made for the modern and for the different women. For those of you who want to feel special, interesting and at the same time extremely modern.</p>
          <p>I am inspired by the world around me and my family, I see fashion in all of its shades and I hope that I can reach all of you with my positive energy. The world of fashion is my passion and I will share it with you with pleasure.</p>`,
        values: `<p>Sustainable fashion is the way to go. We are using more and more natural materials that are good for our clients comfort and skin touch, but also natural fibers are originally environment friendly.</p>
          <p>Most of the garments are handmade by our team, which is a guarantee of high quality.</p>`,
        instagram: "",
      }
    } else if (a.includes("/lino-piani")) {
      return {
        name: "Lino Piani",
        banner: "/store/20180730431/images/lino-piani-carousel.jpg",
        balodanaSays:
          "Lino Piani combines old-world European tailoring skill with a fresh perspective on style and grace in womens fashions. He is a rare tailor who can also do fully bespoke items, so your imagination can combine with his for true artistic outcomes. All of their garments are intended to last a lifetime and be passed to future generations, a rare thing indeed today.",
        year: 2016,
        from: "London, England",
        logo: Lino,
        longDesc: `<p><strong>Celebrate Your Individuality</strong></p>
          <p>In a world where you can be anything, be yourself. Create a garment that feels authentic, soulful, and honest. Design by you for you.</p>
          <p><strong>Bespoke & Made-to-Measure Ladies Wear</strong></p>
          <p>Lino Piani offers bespoke couture and made to measure service for women with focus on expert tailoring and fit. Born from Italian heritage and deep connection with craft of fashion design, Lino Piani invites you to create unique garments, designed to be cherished forever.</p>
          <p><strong>Design your story</strong></p>
          <p>How we dress is an expression of who we are. When you create a unique garment, your personal style is the most important element of the entire process. Whether you choose a lining that pops, a hidden ruffle or an asymmetric cuff, every detail speaks volume.</p>
          <p><strong>Queen for a Day</strong></p>
          <p>You give your time and energy away freely, but this is an opportunity to invest in yourself. The process of creating a bespoke or made-to-measure piece celebrates your personal style, your figure and your personality. This one moment is all about you.</p>
          <p><strong>The Value of Individuality</strong></p>
          <p>A perfect tailored suit, a made-to-measure jacket or a bespoke dress will last a lifetime, and can be altered as your shape changes over time. Its value is immeasurable because you cant put a price tag on confidence, self-expression and individuality.</p>
          <p><strong>My Journey to Fashion</strong></p>
          <p>I was raised in Italy by my mother who was a couturier and tailor. In those days, dressmakers worked from their homes and ladies of the town would come to be measured and fitted for their dresses.</p>
          <p>In this intimate setting I would see women enjoying one small moment where their personality, their style and their form took center stage.</p>
          <p>My mother instilled in me a true love and passion for bespoke and couture clothing design. But more importantly, she taught me how this work can transform people.</p>
          <p>I have built my business around this single idea. I want my skill and craft to merge with your vision to create a garment that fills you with confidence and joy.</p>`,
        values: `<p><strong>Creating Sustainable Luxury</strong></p>
          <p>As modern, ethical fashion brand, Lino Piani promotes dignity and responsibility at every level of our global fashion industry. Driven by a sense of obligation to ignite positive change, both as a brand and as person, Lino Piani believes that true luxury is a sustainable force.</p>
          <p>Lino Piani guarantees that all our fabrics, linings, trimmings and other components are from Italy, France, Great Britain and Switzerland.</p>
          <p>All garments are made by highly trained tailors, artisans and seamstresses of the venerable Savile Row community of London.</p>`,
        instagram: "",
      }
    } else if (a.includes("/samshek")) {
      return {
        name: "Samshk",
        banner: "/store/20180730431/images/Samshek_Carousel_1.jpg",
        balodanaSays: `Youre only young once...or so they say. We disagree! We love the hip, young styles that Samshek offers to women of any age, along with classics and basics for work and play. Youll find these garments are well constructed and have lovely details you wouldnt expect in this price point. The duo has a very successful store in India and brings their designs to America with exclusive offerings on Balodana.`,
        year: 2017,
        from: "Seattle, Washington - USA",
        logo: Samshek,
        longDesc: `<p>Samshek was initiated by two siblings making it happen while being in two different parts of the globe dreaming of changing the way an individual buys clothing. When Sam was graduating from London College Fashion, she came across the idea of customization to help women reflect their individuality through their wardrobe of affordable custom clothing. She discussed the concept with her brother who blended the concept of customization with modern era technology to accelerate the process of manufacturing and making a quick delivery while making it affordable for the customers. Sam leads the design team with the aim of producing trendy and comfortable designs for Samsheks happy customers.</p>
          <p>Both the siblings were influenced by the ethical practices followed by their family business and thought of bringing a change in the clothing industry within the Indian region. All the products are proudly made in India with high quality and ethically sourced material. The founders plan to make a change in the industry by setting up a large scale awareness campaign in the near future for increase in average wages of seamstresses, prohibiting sweatshops and improving working conditions for employees in India, which are majorly exploited by Unicorns of Fast Fashion market.</p>`,
        values: `<p>Samshek uses premium fabrics, which are ethically sourced and produced without using any harmful chemicals. A virtual inventory system allows Samshek to follow a zero waste policy while crafting each outfit and creating no waste before and after the manufacturing process. Samshek is following the direction of sustainability and environment friendly practices from fabrics to customer satisfaction.</p>`,
        instagram: "",
      }
    } else if (a.includes("/shantima")) {
      return {
        name: "Shantima",
        banner: "/store/20180730431/images/shantima_carousel_1.gif",
        balodanaSays: `If we had to choose a capsule wardrobe to wear the rest of our lives, theyd be Shantima. Dreamy, dancey and flowing, the combination of their gorgeous linen textures, natural colors and flattering lines will spoil you for anything else. We also like that theyre designed like Garanimals for adults so you can easily mix and match.`,
        year: 2012,
        from: "Israel",
        logo: Shantima,
        longDesc: `<p>Shantima is a small clothing design studio located in southern Israel.</p>
          <p>Our goal is to help express the harmony between external and internal beauty in every person who comes to us, so we create high-quality linen clothing that truly reflects the individuality of its owner.</p>
          <p>Everything is made to measure and considers the person's height, body type and physical characteristics. We make all kinds of separates, and we also make exclusive capsules that feature 3-7 pieces in each. They are compatible with each other not only in color but also in terms of functionality - thereby the problem of how to combine them is instantly solved.</p>
          <p>In our design, we strive for simplicity and comfort and prefer direct and trapezoidal silhouettes, laconic cut and layering. We pay great attention to the color palette of fabrics and its diversity. We rarely use fasteners, and instead prefer tapes, ties and elastic bands which increase comfort and flexibility.</p>
          <p>Eco-friendly and fully biodegradable linen has natural antiseptic and antibacterial properties. It doesn't create static and protects against ultraviolet radiation. It also allows the body to breathe! That's why clothes made from linen fabrics are so comfortable, particularly in warm weather. Linen can be layered and worn year-round, though.Linen is very wear-resistant and with time it only gets better. A high quality tailoring ensures that you are most likely to never wear our clothes out!</p>
          <p>Shantima is a community of like-minded people and friends around the globe. We easily recognize each other in the crowd, even if we speak different languages. To be among us, you don't need a club card, perfect appearance or special connections. The main thing is to understand where and why you are going, and have the desire to follow the call of your heart.</p>
          <p>We believe there is nothing in this life that happens just by chance. This also means that your meeting with us was not accidental either.</p>
          <p>Welcome to Shantima!</p>`,
        values: `<p>You recognize them easily.</p>
          <p>Bright, authentic, harmonious.</p>
          <p>People who have found their way.</p>
          <p>They are free. Free to be themselves. At any place, time or state of mind.</p>
          <p>It is so simple and so difficult at the same time - to follow where your Heart leads you and not to betray yourself on the way, right?</p>
          <p>But here at Shantima we know that there is nothing more important.</p>
          <p>We make clothes for such people. People who choose to follow their own way, feeling the call deeply inside.</p>
          <p>People who are looking for their true selves, always, whenever and wherever.</p>
          <p>People, doing what they can't not to do.</p>
          <p>Shantima designs are all made of linen, because we believe that it is a special material, and its eco-friendly qualities are as important to us as how it feels.</p>
          <p>Did you know? Linen clothing gets less dirty, due to the special structure of the fabric with even and smooth hairs on the surface that repel dirt! So you dont have to wash it very often, which saves water. And all our designs are washable linen, no dry cleaning or chemicals required.</p>
          <p>The sustainability of our fabric production is also very important for us. We buy linen from reliable and transparent sources so we know the whole process of fabric manufacturing, and where possible we use the work of local specialists.</p>
          <p>We minimize waste in our production process: if we have remnants, the fabric is used for creating new designs that will complement our collection: belts, bags, wallets.</p>`,
        instagram: "",
      }
    } else if (a.includes("/sumissura")) {
      return {
        name: "Sumissura",
        banner: "/store/20180730431/images/sumissura_carousel_3.jpg",
        balodanaSays: `A top provider of European tailored clothing, Sumissura is a brand with many years of experience in the realm of made-to-measure and custom fashion. Theyre a go-to for completely custom (down to the button-thread color!) workwear, and have generous professional and business casual offerings.
A Sumissurra blazer or dress is one that youll pull out of the closet time and again. Their prices are very affordable given their custom tailoring and customization options.`,
        year: 2012,
        from: "Zurich, Switzerland",
        logo: Sumissura,
        longDesc: `<p>The name Sumissura roughly translates to made-to-measure in Italian, as creating personal masterpieces for every woman to wear is one of the main goals of this shop. The name definitely fits the vision.</p>
          <p>Sumissura understands that every woman today is on a tight schedule and has little time to try on outfits. That is why this online shop has been designed to be as streamlined as possible.</p>
          <p>Sumissura allows women to express their very own personal styles through the suits, shirts, and slacks they wear. In addition, users are given the chance to customize every garment with unique personalization options to add a distinctive touch. Much like the world around us, fashion is constantly changing. Sumissura wants to assure every woman that they too can keep up with the latest trends. With a catalogue of more than 180 fabrics, Sumissura enables women to find a fabric they love. Offering all of this and more at affordable prices, Sumissura makes sure not to sacrifice at all in garment quality.</p>`,
        values: `<p>Sumissura Is an ethical fashion brand. It is very important to us to emphasize this concept because we really believe that you buy our products not only because they fit you like a glove but also because they are produced ethically.</p>
          <p>Our tailors working in our factory in Shanghai have fair contracts and enjoy medical and other comprehensive benefits for themselves and their families, according to a sustainable business model that does not rely on exploitation. They dont just have a job; they have careersmost of them having been with our company since we started this adventure in 2012.</p>
          <p>Our commitment to sweatshop-free labor practices is a long-term investment and a demonstration that an ethical and sustainable business is viable. The employees of Sumissura would like to thank you as every time you choose our products, you are supporting these sustainable practices too.</p>`,
        instagram: "",
      }
    } else if (a.includes("/tavrovska")) {
      return {
        name: "Tavrovska",
        banner: "/store/20180730431/images/tavrovska-carousel-2.jpg",
        balodanaSays: `We are mad for the Mad Men style that TAVROVSKA evokes. Its got a hint of vintage but is quite modern in colors, styles and fabrics. Exactly what professional women crave: beautiful silhouettes and unique styles for office and play.`,
        year: 2015,
        from: "Kiev, Ukraine",
        logo: Tavrovska,
        longDesc: `<p>TAVROVSKA is a contemporary Kiev-based womenswear label, focused on creating beautiful and wearable garments to suit the busy lifestyle of the professional woman, in and out of the office.</p>
          <p>Our designs are defined by feminine lines and smart detail. We work closely with our suppliers to create fabrics exclusively for our collection. A lot of our pieces require hand craftsmanship so our manufacturers have worked tirelessly to indulge you in garments with the finish and soul of true pieces.</p>
          <p>Form follows function but design is never compromised in its pursuit. We are happy to help women be at their best and achieve their dreams. Its so much easier to succeed in life when you feel confident in your clothes!</p>`,
        values: `<p>Building great and healthy relations among employees is the essential task for our team. We are working hard to keep the spirit of partnership, to give the feeling that everyone here works for a common goal and at the same time everyone can be themselves at work.</p>
          <p>We never stop investing in the training of our employees and in the quality of working conditions.</p>
          <p>The safety of employees at the workplace and in everyday life is great importance to the company. We care about their future, we create special social programs. We share the same goals of eliminating child and adult forced labor, and we are fully aligned with the spirit and purpose of Responsible Sourcing Networks cotton pledge.</p>
          <p>Our corporate social responsibility program also includes a TAVROVSKA supplier audit program.</p>
          <p>We are confident that in order to create a quality product, all processes in its creation must be of high quality.</p>`,
        instagram: "",
      }
    } else if (a.includes("/teyxo")) {
      return {
        name: "Teyxo",
        banner: "/store/20180730431/images/teyxo_carousel_2.jpg",
        balodanaSays: `We love Teyxo for the versatility and affordability of their styles. Designer Lara Popa is an expert at proving popular opinion wrong: clothing can be funky and colorful and still be professional.
Teyxo fashion is vibrant and edgy, providing comfy yet stylish garments. You will stand out and turn heads wearing Teyxo! Plus size ladies will especially love the forgiving and flattering designs that make you feel sensuous, flirty and fun.`,
        year: 2014,
        from: "Galati, Romania",
        logo: Teyxo,
        longDesc: `<p>TEYXO is an independent brand, based in Romania, offering custom made clothing for women. Since 2014, Teyxo has offered a wide variety of products from asymmetric dresses, tunics, and coats to plus size pants, vests and tops. Mixing outstanding designs with quality fabrics, Teyxo creates a wide range of colors and cuts. Their fun and playful designs achieve the perfect union of elegance, freedom, comfort and beauty.</p>
          <p>Founder Lara Popa is not just an entrepreneur and fashion designer, shes also a successful actress and filmmaker, as well as publisher of a lifestyle site TeyxoStyle to showcase her passions of fashion, travel and celebrating amazing women. Fashion is Laras family businessher mother owned a clothing manufacturing business and the two collaborate frequently in the creation of the Teyxo lines.</p>`,
        values: `<p>For all our items, we use imported fabrics from the best suppliers, typically in Italy and Turkey. If you ask us #WhoMadeMyClothes, there is a whole team involved in the process of making your garments!</p>
          <p>Our plant is in Romania, and all of our employees are local. After we make the first design, we have someone in our team who receives the pattern and scales it for every standard size. This is the person who, when we get an order, cuts the fabric according to the size's pattern and then adjusts for the measurements of that customer.</p>
          <p>After this, we have another person who measures everything once again and double checks the fabric for flaws. Then, another person takes over and starts putting the pieces together at the sewing machine. In some cases if the item has buttons, studs, or other accessories, someone else is in charge of that process. When everything is done, the person who initially checked the fabric takes the final item, cleans up dangling threads, carefully checks the measurements and the design, irons it and then gets it ready to ship to you!</p>`,
        instagram: "",
      }
    } else if (a.includes("/zaza")) {
      return {
        name: "Zaza Boutique",
        banner: "/store/20180730431/images/zaza_boutique_carousel_3.jpg",
        balodanaSays: `ZaZa Boutique is the epitome of elegance. They flirt with brilliant fabrics and textures, producing a stunning collection of skirts, dresses, and blouses. ZaZa is especially known for their exotic hand-beadwork and traditional Romanian designs.
We love going to ZaZa when were feeling like exuding femininity and class. We also love that the two founders are women, so they really understand our bodies.`,
        year: 2011,
        from: "Bucharest, Romania",
        logo: Zaza,
        longDesc: `<p>Zaz It we always say, which is synonymous with pieces that make a woman feel fun and feminine. ZaZa Boutique is a brand that we established 7 years ago in order to personalize shopping. We were one of the first brands selling made-to-measure collections online, ready to ship in 7 business days, no matter the garment.</p>
          <p>We decided to establish ZaZa because we were stubborn about not wanting standardized clothes ourselves. As classical tailoring is a declining art, it left us no choice but to make our own collections piece by piece, customer by customer.</p>
          <p>We try to channel our couture know-how into a lineup of looks suited to more dressed-up occasions. Our collections strike a wearable balance between fun, seductive, and sophisticated. We use luxurious fabrics and classic cuts, but with a modern twist.</p>`,
        values: `<p>We strongly promote the idea that it is best to have well-fitting clothes in your closet, rather than a huge horde of ill-fitting ones. Thats why we make sure not to create fad items and to use extremely high quality fabricswe want to create clothing that lasts. Our fabrics are typically sourced from within Europe, and we look for durability as the main criteriaas well as luxurious color and textureso that clothes will last and not fall apart like fast fashion garments do.</p>
          <p>We all have kids who we love and care about future generations, so we try to make as little waste as possible. We use our leftover fabric to make stuffed dolls, which we donate to kindergartens.</p>
          <p>Our garments are made in our shop in Bucharest, and all our employees are paid a fair wage with benefits. We are committed to keeping a small carbon footprint and providing fair labor practices for our employees.</p>`,
        instagram: "",
      }
    } else {
      return null
    }
  }

  // modal
  const [open, setOpen] = React.useState(false)

  const handleOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const designer = findDesigner(data.url)
  console.error("DESIGNER HERE", designer)
  return (
    <>
      <div className={classes.outerWrapper}>
        <div
          className={classes.subNavWrapper}
          style={{
            background: `url(${PROJECT_LINK}${designer.banner}`,
          }}
        ></div>
        <div className={classes.innerWrapper}>
          <div className={classes.logo}>
            <img src={designer.logo} alt="" />
          </div>
          <div className={classes.brandInfo}>
            <div className={classes.brandInfoInner}>
              <div>
                <h1 className={classes.brandName}>{designer.name}</h1>
                <div className={classes.established}>
                  <span style={{ color: "#737079", marginRight: "10px" }}>
                    Established {designer.year}
                  </span>
                  <span>
                    <strong>{designer.from}</strong>
                  </span>
                </div>
                <div className={classes.shortDesc}>
                  <p>"{designer.balodanaSays}"</p>
                </div>
              </div>
              <div className={classes.readOurStoryBtn}>
                <div className={classes.readWrapper} onClick={handleOpen}>
                  <div className={classes.bookIcon}>{/* <Book /> */}</div>
                  <span>Read our story</span>
                </div>
              </div>
            </div>

            <div>
              <span className={classes.tag}>Tag 1</span>
              <span className={classes.tag}>Tag 2</span>
              <span className={classes.tag}>Another Tag</span>
            </div>
          </div>
        </div>
      </div>

      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={handleClose}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <div onClick={handleClose} className={classes.closeBtn}>
              <i className="material-icons">close</i>
            </div>
            <div className={classes.modalInnerTop}>
              <div className={classes.modalTopGrid}>
                <div className={classes.modalTopLeft}>
                  <div
                    style={{ background: `url('${designer.logo}')` }}
                    className={classes.modalLogo}
                  ></div>
                  <div className={classes.modalInnerMain}>
                    <h3>Designed in</h3>
                    <span>{designer.from}</span>
                    <h3>Instagram</h3>
                    <span>{designer.instagram}</span>
                    <h3>Established</h3>
                    <span>{designer.year}</span>
                  </div>
                </div>
                <div className={classes.modalTopRight}>
                  <h2 className={classes.modalHeading}>
                    About {designer.name}
                  </h2>
                  <div
                    className={classes.modalText}
                    dangerouslySetInnerHTML={{ __html: designer.longDesc }}
                  ></div>
                  <h2 className={classes.modalHeadingValues}>Brand Values</h2>
                  <div
                    className={classes.modalText}
                    dangerouslySetInnerHTML={{ __html: designer.values }}
                  ></div>
                </div>
              </div>
            </div>
            <div className={classes.modalInnerBottom}></div>
          </div>
        </Fade>
      </Modal>
    </>
  )
}
export default BDDesignerCategoryHeader
