import React, { useState, useContext, useRef, useEffect } from "react"
import { useSelector, useDispatch, shallowEqual } from "react-redux"

import { useLocation } from "@reach/router"

import Grid from "@material-ui/core/Grid"
import Footer from "../AC-Footer/BDFooter.jsx"
import "../../assets/css/menu.css"
import "../../assets/css/minified.css"

import BDDesignerHeader from "./components/CategoryHeader/BDDesignerHeader"
import CollectionsHeader from "../AC-CategoryPage/components/CategoryHeader/BDCollectionsHeader"
import CategoryItems from "./components/CategoryItems/BDCategoryItems"
import Facets from "./components/AC-Facets/BDFacets"
import Pagination from "./components/BDPagination"
import PaginationRegular from "../AC-Pagination/BDPagination"

import NumberOfItems from "./components/NumberOfItems"
import Loading from "../AC-Loading/Loading.jsx"
import FacetBreadcrumb from "../AC-Breadcrumb/BDFacetBreadcrumb.jsx"
import SortBy from "./components/SortBy"
import Facets_Mobile from "../AC-Facets/Facets_Mobile.jsx"
import { renderPlaceholderCategoryItems } from "../AC-CategoryPage/components/CategoryItems/CategoryItems.jsx"
import NoItems from "../AC-CategoryPage/components/NoItems/NoItems"
import classes from "./styles/BDCategory.module.css"

import {
  dispatchScroolPage,
  nextPage,
} from "../../redux/actions/paginationActions.js"
import { I18nContext } from "../../i18n"

import CategoryHeader from "./components/CategoryHeader/BDCategoryHeader"
import { gatsbyFetchCategory } from "../../redux/actions/categoryActions.js"

function BDCategory({ data, pageContext }) {
  const { translate } = useContext(I18nContext)
  const [filterButtonClicked, setFilterButtonClicked] = useState(false)

  const location = useLocation()

  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(gatsbyFetchCategory(data.facets, data.cid, data.cat))
  }, [])

  const isMobileState = useSelector(
    state => state.mainReducer.isMobile,
    shallowEqual
  )

  const loadingBottom = useSelector(
    state => state.categoryReducer.loadingBottom,
    shallowEqual
  )

  const renderHeader = () => {
    if (location.pathname.includes("collections/")) return <CollectionsHeader />
    else if (location.pathname.includes("designer")) {
      console.error("THIS SHOULD WORK")
      return <BDDesignerHeader data={data} />
    } else {
      console.error("NOT THIS WORK")
      return <CategoryHeader data={data} />
    }
  }

  const pagesState = useSelector(
    state => state.categoryReducer.pages,
    shallowEqual
  )

  const noItemFoundState = useSelector(
    state => state.categoryReducer.noItemFound,
    shallowEqual
  )

  const currentPageState = useSelector(
    state => state.categoryReducer.currentPage,
    shallowEqual
  )

  const scroolPageState = useSelector(
    state => state.categoryReducer.scroolPage,
    shallowEqual
  )

  const cidState = useSelector(
    state => state.categoryReducer.cidN,
    shallowEqual
  )

  const [isServerSide, setIsServerSide] = useState(true)

  const breadState = useSelector(
    state => state.facetReducer.bread,
    shallowEqual
  )
  const urlFilterParamsState = useSelector(
    state => state.facetReducer.urlFilterParams,
    shallowEqual
  )

  const clientSideCategoryItemState = useSelector(
    state => state.categoryReducer.categoryItems,
    shallowEqual
  )

  useEffect(() => {
    if (
      (breadState.length > 0 || urlFilterParamsState !== "") &&
      clientSideCategoryItemState[0].id !== ""
    ) {
      setIsServerSide(false)
    } else if (breadState.length === 0) {
      setIsServerSide(true)
    }
  }, [breadState, clientSideCategoryItemState, urlFilterParamsState])

  const handleNextPage = page => {
    if (scroolPageState < pagesState.length || page != 0) {
      dispatch(dispatchScroolPage(page))
      dispatch(nextPage(page, cidState, true))
    } else {
      console.error("End of the pagea")
    }
  }

  const handleFilterButtonClicked = () => {
    document.getElementById("root").setAttribute("aria-hidden", true)
    setFilterButtonClicked(true)
  }

  const handleFacetContentCloseIconClicked = () => {
    document.getElementById("root").setAttribute("aria-hidden", false)
    setFilterButtonClicked(false)
  }

  const renderFacets = (
    <React.Fragment>
      <button
        className={classes.mobileFilterBtn}
        onClick={() => handleFilterButtonClicked()}
      >
        {translate("mobile_facets.filterButtonText")}
        <i className="material-icons">filter_list</i>
      </button>
      <Facets_Mobile
        filterButtonClicked={filterButtonClicked}
        handleFacetContentCloseIconClicked={handleFacetContentCloseIconClicked}
      />
    </React.Fragment>
  )

  const renderMobileFacets = <Facets data={data} />

  const myRef = useRef(null)

  const renderCollectionPlaceHolders = () => {
    return (
      <React.Fragment>
        <Grid container>
          <Grid container className="item-card-container">
            <Grid item className="item-card-item" xs={12} sm={6} md={4} lg={4}>
              <div
                className="placeholder-item-card-wrapper"
                style={{
                  boxShadow: "0px 0px 1px 0px #c8c8c8",
                  marginLeft: "10vw",
                }}
              >
                <div
                  style={{
                    backgroundColor: "#fff",
                    height: "50vh",
                    width: "35vw",
                  }}
                ></div>
                <div>
                  <div
                    style={{
                      backgroundColor: "#f5f5f5",
                    }}
                  ></div>
                </div>
              </div>
            </Grid>
            <Grid item className="item-card-item" xs={12} sm={6} md={4} lg={4}>
              <div
                className="placeholder-item-card-wrapper"
                style={{
                  boxShadow: "0px 0px 1px 0px #c8c8c8",
                  marginLeft: "15vw",
                }}
              >
                <div
                  style={{
                    backgroundColor: "#fff",
                    height: "40vh",
                    width: "17vw",
                  }}
                ></div>
                <div>
                  <div
                    style={{
                      backgroundColor: "#f5f5f5",
                    }}
                  ></div>
                </div>
              </div>
            </Grid>
            <Grid item className="item-card-item" xs={12} sm={6} md={4} lg={4}>
              <div
                className="placeholder-item-card-wrapper"
                style={{
                  boxShadow: "0px 0px 1px 0px #c8c8c8",
                  marginLeft: "2vw",
                }}
              >
                <div
                  style={{
                    backgroundColor: "#fff",
                    height: "40vh",
                    width: "17vw",
                  }}
                ></div>
                <div>
                  <div
                    style={{
                      backgroundColor: "#f5f5f5",
                    }}
                  ></div>
                </div>
              </div>
            </Grid>
          </Grid>
        </Grid>
      </React.Fragment>
    )
  }

  if (noItemFoundState) {
    return <NoItems />
  }

  if (location.pathname.includes("collections/")) {
    return (
      // Collections layout
      <div id="bd">
        <div className={classes.collectionsWrapper}>
          {renderHeader()}
          <CategoryItems data={data} pageContext={pageContext} />
        </div>

        <Footer />
      </div>
    )
  } else {
    return (
      // Regular Layout
      <div id="bd">
        <div className="main">
          {renderHeader()}
          <div className={classes.wrapper}>
            <div>{!isMobileState ? renderMobileFacets : null}</div>
            <div>
              <div className={classes.facetBreadcrumbWrapper}>
                <FacetBreadcrumb data={data} />
              </div>
              {isMobileState ? renderFacets : null}
              <div className={classes.subWrapper}>
                <div className={classes.numOfItemsWrapper}>
                  <NumberOfItems data={data} />
                </div>
                <div className={classes.sortByPagination}>
                  <div>
                    <SortBy />
                  </div>
                  <div>
                    {isServerSide ? (
                      <Pagination data={data} pageContext={pageContext} />
                    ) : (
                      <PaginationRegular data={data} />
                    )}
                  </div>
                </div>
              </div>

              <CategoryItems data={data} pageContext={pageContext} />
              {loadingBottom ? <Loading /> : null}
              <div ref={myRef} />
              {!loadingBottom && scroolPageState >= 4 ? (
                <div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <button
                    style={{
                      backgroundColor: "#222e50",
                      borderRadius: "0",
                      boxShadow: "none",
                      fontWeight: "700",
                      textTransform: "uppercase",
                      letterSpacing: "2px",
                      lineHeight: "1.42857143",
                      padding: "6px 15px",
                      fontSize: "16px",
                      color: "white",
                    }}
                    onClick={() => handleNextPage(scroolPageState + 1)}
                  >
                    More Items
                  </button>
                </div>
              ) : null}
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default BDCategory
